import Vue from 'vue'

import Drop from './Drop.vue'
import Dropdown from './Dropdown.vue'
import Select from './Select.vue'
import LoadingAnimation from './LoadingAnimation.vue'
import LoadingOverlay from './LoadingOverlay.vue'
import Graph from './Graph.vue'
import Tabs from './Tabs.vue'
import Tab from './Tab.vue'

Vue.component('ui-drop', Drop)
Vue.component('ui-dropdown', Dropdown)
Vue.component('ui-select', Select)
Vue.component('ui-loading-animation', LoadingAnimation)
Vue.component('ui-loading-overlay', LoadingOverlay)
Vue.component('ui-graph', Graph)
Vue.component('ui-tabs', Tabs)
Vue.component('ui-tab', Tab)



